import { useState, useRef, useEffect } from 'react'
import { WriteSubtitle, WriteTitle } from '../../atoms/Title'
import Layout from '../../organisms/Component/Layout'
import DefaultInput, { Input } from '../../atoms/Input/DefaultInput'
import DropBox from '../../atoms/DropBox/DropBox'
import { SkillList } from '../../atoms/SkillList'
import { AddBtn, ImgBtn } from '../../atoms/Button'
import { uploadImg } from '../../../utils'
import { domainList, careerList } from '../../../data/profileDropbox'
import LicatFace from '../../../assets/icon-liacat.svg'
import * as styles from './Profile-style'
import { MainBtn } from '../../atoms/Button'
import { useDefaultProfileStore } from '../../../store/DefaultProfileStore'
import { GetCommitRecord } from '../../atoms/Github'
import {
  getStoreEmailPart,
  parsePhoneNumber,
  joinEmail,
  createSkillList,
  deleteSkillItem,
} from '../../../utils/profileUtils'
import { moveToTop } from '../../../utils/scrollEvent'

export default function DefaultProfile() {
  const { updateDefaultProfile } = useDefaultProfileStore()
  const storedData = JSON.parse(localStorage.getItem('makere-default-profile'))

  const fileRef = useRef(null)
  const skillListRef = useRef(null)

  /**
   *      
   * @param {string}  key -    key
   * @return {string | number | [string]}       value
   */
  const getStoredData = (key) => {
    if (storedData) {
      return storedData[key]
    } else {
      if (key === 'skills' || key === 'github') {
        return []
      } else {
        return ''
      }
    }
  }

  //    state
  const [profileImg, setProfileImg] = useState(getStoredData('profileImg')) //  
  const [name, setName] = useState(getStoredData('name')) // 
  const [enName, setEnName] = useState(getStoredData('enName')) //  
  const [phoneNumber, setPhoneNumber] = useState(getStoredData('phoneNumber')) // 
  const [blog, setBlog] = useState(getStoredData('blog')) //   
  const [fullEmail, setFullEmail] = useState(getStoredData('fullEmail')) // 
  const [careerLength, setCareerLength] = useState(
    storedData ? storedData['careerLength'] : ''
  ) // 
  const [skills, setSkills] = useState(getStoredData('skills')) //  
  const [github, setGithub] = useState(getStoredData('github')) // Github

  //   state
  const [emailId, setEmailId] = useState(getStoreEmailPart(0, fullEmail))
  const [emailDomain, setEmailDomain] = useState(
    getStoreEmailPart(1, fullEmail)
  )

  useEffect(() => {
    const email = joinEmail(emailId, emailDomain)
    setFullEmail(email)
  }, [emailId, emailDomain])

  /**
   *     ,   val  
   */
  const saveLocalStorage = () => {
    const val = {
      profileImg,
      name,
      enName,
      phoneNumber,
      blog,
      fullEmail,
      careerLength,
      skills,
      github,
    }

    if (!name.trim()) {
      alert(' .')
    } else {
      localStorage.setItem('makere-default-profile', JSON.stringify(val))
      alert(' .')
    }
  }

  return (
    <>
      <Layout>
        <styles.Section>
          <styles.TitleCont>
            <WriteTitle
              title=" "
              description="      ."
            />
            <MainBtn type="save" onClick={saveLocalStorage}>
               
            </MainBtn>
          </styles.TitleCont>
          <styles.ProfileCont>
            <styles.ImgCont>
              <styles.ImgLabel
                ref={fileRef}
                htmlFor="profile-upload"
                className="profileImg"
              >
                <styles.ImgWrap>
                  <styles.Img
                    src={profileImg ? profileImg : LicatFace}
                    alt={`${name || enName || ''}   `}
                  />
                  {profileImg ? (
                    <ImgBtn
                      type="delete"
                      onClick={() => {
                        setProfileImg('')
                      }}
                    />
                  ) : (
                    <ImgBtn
                      type="add"
                      onClick={() => {
                        fileRef.current.click()
                      }}
                    />
                  )}
                </styles.ImgWrap>
              </styles.ImgLabel>
              <input
                className="profileInput"
                type="file"
                accept="image/*"
                id="profile-upload"
                onChange={(e) => uploadImg(e, profileImg, setProfileImg)}
              />
            </styles.ImgCont>
            <div>
              <styles.InputCont>
                <DefaultInput
                  essentialMsg="*  ."
                  id="name"
                  name="name"
                  type="text"
                  placeholder=") "
                  width="220px"
                  marginRight="12px"
                  inputData={name}
                  onChange={(e) => {
                    updateDefaultProfile(e)
                    setName(e.target.value)
                  }}
                >
                  
                </DefaultInput>
                <DefaultInput
                  id="enName"
                  name="enName"
                  type="text"
                  placeholder=") Kildong Hong"
                  width="356px"
                  inputData={enName}
                  onChange={(e) => {
                    updateDefaultProfile(e)
                    setEnName(e.target.value)
                  }}
                >
                   
                </DefaultInput>
              </styles.InputCont>
              <styles.InputCont>
                <DefaultInput
                  id="phoneNumber"
                  name="phoneNumber"
                  type="tel"
                  placeholder=") 010-1234-5678"
                  width="220px"
                  marginRight="12px"
                  inputData={phoneNumber}
                  onChange={(e) => {
                    updateDefaultProfile(e)
                    setPhoneNumber(parsePhoneNumber(e.target.value))
                  }}
                >
                  
                </DefaultInput>
              </styles.InputCont>
              <styles.InputCont>
                <DefaultInput
                  id="emailId"
                  type="text"
                  placeholder=") paul-lab"
                  width="220px"
                  marginRight="12px"
                  inputData={emailId}
                  onChange={(e) => {
                    setEmailId(e.target.value)
                  }}
                >
                  
                </DefaultInput>
                <span>@</span>
                <DefaultInput
                  id="emailDomain"
                  type="text"
                  placeholder=") paul-lab"
                  width="200px"
                  marginRight="8px"
                  inputData={emailDomain}
                  onChange={(e) => {
                    setEmailDomain(e.target.value)
                  }}
                />
                <DropBox
                  type="email"
                  width="131"
                  list={domainList}
                  emailDomain={emailDomain}
                  setDomain={setEmailDomain}
                />
              </styles.InputCont>
              <styles.InputCont>
                <DefaultInput
                  id="blog"
                  name="blog"
                  type="url"
                  inputData={blog}
                  onChange={(e) => {
                    updateDefaultProfile(e)
                    setBlog(e.target.value)
                  }}
                >
                    
                </DefaultInput>
              </styles.InputCont>
              <styles.Label></styles.Label>
              <DropBox
                width="179"
                type="career"
                careerLength={careerLength}
                setCareerLength={setCareerLength}
                list={careerList}
              />
            </div>
          </styles.ProfileCont>
        </styles.Section>
        <styles.Line />
        <styles.Section>
          <WriteSubtitle subtitle=" " id="skills" />
          <form
            className="inputWrap"
            id="addSkillsListForm"
            onSubmit={(e) => {
              e.preventDefault()
              createSkillList(skillListRef, skills, setSkills)
            }}
          >
            <Input
              id="skills"
              type="text"
              placeholder=") Python"
              width="260px"
              ref={skillListRef}
            />
            <AddBtn form="addSkillsListForm" />
          </form>
          <styles.SkillListWrap>
            {skills.map((skill, idx) => (
              <SkillList
                key={idx}
                type="delete"
                onClick={() => deleteSkillItem(idx, skills, setSkills)}
              >
                {skill}
              </SkillList>
            ))}
          </styles.SkillListWrap>
        </styles.Section>
        <styles.Line />
        <styles.Section>
          <WriteSubtitle subtitle="GitHub" id="github" />
          <GetCommitRecord github={github} setGithub={setGithub} />
        </styles.Section>
      </Layout>
      <MainBtn
        type="profile"
        onClick={() => {
          saveLocalStorage()
          moveToTop()
        }}
      >
        
      </MainBtn>
    </>
  )
}
